<template>
    <div class="tab-pane call-detail  border-0 p-0" id="tabs-4" role="tabpanel" aria-labelledby="Attachment-tab">
        <div class="DivFullDetail col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
            <div class="col-lg-12 mt-3 p-0">
                <div class="heading-border mb-2">
                    <h5 class="h5">
                        <span>{{$t('Attachments')}}</span>
                    </h5>
                </div>
            
            
                <div id="divAllFile" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0"  v-for="(item, index) in AttachmentDivData"
          :key="`AttachmentInput-${index}`">
                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left" id="attach">
                        <div class="form-group" id="attachName">
                            <label>{{$t('AttachmentName')}}</label>
                            <input type="text"  v-model="item.AttachmentName"  :id="'attachName'+index"  class="form-control" maxlength="50"/>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left" id="divchkIsTermsAndCondition">
                        <div class="form-group">
                            <label>{{ $t('TERM_AND_CONDITIONS') }}:</label>
                            <input type="checkbox"  class="custom-control custom-checkbox"  v-model="item.IsTermsAndCondition"  :id="'IsTermsAndCondition'+index" />
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left" id="attachFile">
                        <div class="form-group float-left w-100" id="fileName">
                            <label >{{$t('Attachment')}}:</label><a href="javascript:;">
                                <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                            </a>
                            <div class="row" id="divFiles">
                                <div class="col-10 col-sm-8 col-md-8 col-lg-8 col-xl-8 float-left icons">
                                    <div class="custom-file">
                                        <input type="file" class="custom-file-inputs" size="4" name="FileSrc" :key="fileInputKey"
                                                v-on:change="UploadFile($event)" :id="'flFile'+index" />
                                    </div>
                                </div>
                                <div class="col-1 col-sm-1 col-md-1 col-lg-1 col-xl-1 float-left p-0" >
                                <a href="javascript:void(0) " class="actionicons text-danger" v-on:click="RefreshField(index,fileUploaded)" title="Refresh"><i class="fa fa-undo " style="font-size: 36px;"></i></a>
                                </div>
                                <div class="col-1 col-sm-1 col-md-1 col-lg-1 col-xl-1 float-left p-0" v-if="index==0">
                                    <a href="javascript:void(0) " title="Add More" v-on:click="addMore(item,AttachmentDivData)" id="addMore" class="actionicons"><i
                                            class="fa fa-plus-circle text-success" style="font-size: 36px;"></i></a>
                                </div>
                                <div class="col-1 col-sm-1 col-md-1 col-lg-1 col-xl-1 float-left p-0" v-if="index!=0">
                                <a class="round-icon-small btn-danger-light float-none d-inline-block"  v-on:click="removeField(index,AttachmentDivData)"
                                     href="javascript:;" title="Delete"><i
                                    class="recyclebin fa fa-trash"></i></a>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
        <tg-list SearchPosition='none' :IsShowAction="false" :showCheckBox="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="Asset Management" :ListData="AttachmentList"
            :HeaderData="Headers" :callbackfunction="FetchData" 
            :TotalRecords="TotalRecords" 
            :NorecordfoundText="$t('NoRecordfound')"
            :ListDataCallBackFunction="FetchData"
             v-on:PagerButtonClick="pagerMethod"
            >
            <template v-slot:slotdata="{ data }">
        <template v-if="data.column.COLUMN_NAME == 'Action'">
            <span class="text-dark" :title="data.row.Action">
                <a  class="downloadfile" title="Download Attachment"  :fileType="data.row.FileType"  v-on:click="DownLoadFile(data.row)"  fileType="FileType">
                                    <i class="fa fa-download text-primary action-icon"  title="Download Attachment"></i>
                </a>&nbsp;
                <a href="javascript:;" class="deletefile" title="Delete" value="" v-on:click="DeleteFile(data.row)"  :filePath="data.row.FilePath" :fileType="data.row.FileType">
                    <em class="fa fa-trash text-danger action-icon" title="Remove Attachment"></em></a>
                    </span>
            </template>

            <template v-if="data.column.COLUMN_NAME == 'IS_FOR_TERMS_CONDITION'">
            <span class="text-dark" :title="data.row.Action">
              <label>{{data.row.IS_FOR_TERMS_CONDITION == true ? 'YES' : 'NO'  }}</label>
            </span>
            </template>

       

            </template>
        </tg-list>
</div>
  <div class="p-3">
            <dynamic-form lang="en" :buttons="buttons" v-on:OnSubmit="onSubmit"></dynamic-form>
        </div>
    </div>
      
</div>

        </template>


<script>
import DataService from '../../service/DataService'
export default {
    data() {
        return {
            Headers: [{
                "COLUMN_NAME": "SERIAL_NUMBER",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('SerialNumber'),
                "DISPLAY_ORDER": 1,
                
            },
            {
                "COLUMN_NAME": "ATTACHMENT_TITLE",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AttachmentName'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ATTACHMENT_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('FileName'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "IS_FOR_TERMS_CONDITION",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ForTermCondition'),
                "DISPLAY_ORDER": 4,
                settings:{
                    isInSlot : true
                }

            },
            {
                "COLUMN_NAME": "TERMS_CONDITION_STARTS_AT",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TermConditionStartDate'),
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "Action",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Action",
                "DISPLAY_ORDER": 5,
                settings:{
                    isInSlot : true
                }
            },
            ],
            AttachmentList: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            flag:'',
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning HideTab",
                    text: "Previous <i class='fa fa-angle-double-left pr-2'></i>",
                    onClick: this.onPreviousClick
                },
                {
                    type: "submit",
                    class: "btn btn-primary HideTab",
                    text: "Next<i class='fa fa-angle-double-right pl-2'></i>",
                    isVisible: false,
                    onClick: this.onNextClick
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save ",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancelClick
                }
            ],
            AttachmentDivData: [{AttachmentName: '',IsTermsAndCondition:'',Is_DisplayImage: ''}],
            fileUploaded:[],
            fileInputKey:0,
            AssetCatalogId:window.location.href.split('/')[5],
            AssetCatalogData:[],
            IsEnableAssetManagement: false,
            // AttachmentName:[],
            // IsTermsAndCondition:[],
            // Is_DisplayImage:[]
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
        vm.GetAssetCatalogById();
        vm.EnableAssetManagement();
    },
    methods:
    {
        GetAssetCatalogById()
        {
            var vm=this;
            vm.isLoading=true;
            var url = `id=${vm.AssetCatalogId}`;
            DataService.GetById(url).then(response => {
                vm.AssetCatalogData=JSON.parse(response.data.result).data[0];
                vm.isLoading=false;
            })
        },
        DeleteFile(e)
        {
            debugger
            var vm=this;
            var params=`attachmentId=${e.ASSET_ATTACHMENT_ID}&statusId=1003`;    
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("File")), function () {   
                DataService.DeleteFile(params).then(response=>{
                    if(response.data==true)
                    {
                        vm.ShowAlert(vm.$t("File has beed deleted successfully"), "success",vm.$t('Alert'));
                        vm.FetchData();
                    }
                    else{
                        vm.ShowAlert(vm.$t("Unknown  Error"), "failure",vm.$t('Alert'));
                    }
                })
            })
        },
        onCancelClick()
        {
            var vm=this;
            vm.$parent.btnCancel();
        },
       
        onPreviousClick()
        {
            var vm=this;
            vm.$parent.btnPreviousTab();
        },
        UploadFile(event,indx) {
            var vm = this;
            vm.fileUploaded.push(event.target.files[0]);
        },
        RefreshField(indx, field)
        {
            debugger
            var vm=this;
            // vm.fileInputKey[indx]++;
             vm.fileUploaded.splice(indx, 1)
            debugger
        },
        addMore(value, fieldType)
        {
            debugger
           var vm=this;
           if((vm.AttachmentDivData.length +   vm.AttachmentList.length) < 5)
           {
            vm.AttachmentDivData.push({AttachmentName: '', IsTermsAndCondition:'',Is_DisplayImage: ''});
           }
           else
           {
            vm.ShowAlert(vm.$t("Max limit reacheed (Only five attachments are allowed)"), "Warning",vm.$t('Alert'));
           }
           
           setTimeout(function () {vm.CheckBoxBootstrap()}, 100);  
        },
        removeField(index, fieldType) {
            debugger
            fieldType.splice(index, 1);
        },
        EnableAssetManagement: function () {
            let url = `moduleName=Asset&configkey=CanSeeAssetManagement`;
            DataService.EnableAssetManagement(url).then(response => {
                this.IsEnableAssetManagement = response.data;

            });
        },
        onSubmit: function()
        {
            debugger
            var vm=this;
            if(vm.AttachmentDivData.length >=0)
           {
            vm.fileUploaded.forEach((item,index) => {
                vm.AttachmentDivData[index].fileUploaded=item;
            });
           }
          
           if (vm.IsEnableAssetManagement) {
                // vm.isLoading = true
                    let myObj = {};
                    myObj.AttachmentName = Array.prototype.map.call(vm.AttachmentDivData, function(item) { return item.AttachmentName; }).join(",");
                    // myObj.Is_DisplayImage = Array.prototype.map.call(vm.AttachmentDivData, function(item) { return item.fileUploaded })
                    myObj.IsTermsAndCondition = Array.prototype.map.call(vm.AttachmentDivData, function(item) { return item.IsTermsAndCondition; }).join(",");
               
                    myObj.DeployProduct=false;
                    myObj.AssetCatalogueId = vm.AssetCatalogId;
                    myObj.Name = vm.AssetCatalogData.NAME,
                    myObj.LocationId = vm.AssetCatalogData.LOCATION_ID;
                    myObj.AssetTypeId = vm.AssetCatalogData.ASSET_TYPE_ID,
                    myObj.BarcodeTypeId = vm.AssetCatalogData.BARCODE_TYPE_ID,
                    myObj.StatusId = vm.AssetCatalogData.STATUS_ID.toString(),
                    myObj.CreateUniqueNameListing = vm.AssetCatalogData.CREATE_UNIQUE_NAME_LIST,
                    myObj.TotalCost = 0,
                    myObj.AssetCost = 0,
                    myObj.DeployId = 0;
                    myObj.Quantity = 0;
                    myObj.UnitPrice = 0;
                    myObj.ParentUniqueId = "",
                    myObj.ChildUniqueId = "",
                    myObj.ChildCatalogueId = "",
                    myObj.CatalogueId = "",
                    myObj.CatalogHdnId = "",
                    myObj.SelfieEnabled = "",
                    myObj.ChildCategoryName = "",
                    myObj.AssetUniqueName = "",
                    myObj.AssetCatalogName = "",
                    myObj.Description = vm.AssetCatalogData.DESCRIPTION,
                    myObj.AssetTagIdsWizard = "",
                    myObj.AssetTypeTagIdsWizard = "",
                    myObj.Manufacturer = "",
                    myObj.NumberOfItems = "",
                    myObj.AsseetGroupQuantity = "",
                    myObj.ItemCode = "",
                    myObj.ChildItemCode = "",
                    myObj.ChildCatalogName = "",
                    myObj.ChildRequestQuantity = "",
                    myObj.ChildAssetType = "",
                    myObj.AssetType = "",
                    myObj.StandardTypeId = "",
                    myObj.AssetCompartmentId = "",
                    myObj.AssetGroupId = "",
                    myObj.IsSelfCheckout = "",
                    myObj.IsEnableTerms = "",
                    myObj.SubLocationId = "",
                    myObj.Attachment = "",
                    myObj.CreatedAt = "",
                    myObj.CreatedBy = "",
                    myObj.ModifiedAt = "",
                    myObj.ModifiedBy = "",
                    myObj.Asset_Type = "",
                    myObj.Status = "",
                    myObj.ForCompany = vm.AssetCatalogData.MIN_CMP_QUANTITY,
                    myObj.ForEmployee = vm.AssetCatalogData.MIN_EMP_QUANTITY,
                    myObj.MoveToInTransitInfo = "",
                    myObj.AvailableItems = "",
                    myObj.PendingAvailableQuantity = "",
                    myObj.PendingTotalAvailableItems = "",
                    myObj.TotalQuantity = "",
                    myObj.ChildAvailableItems = "",
                    myObj.TotalFilesCount = "",
                    myObj.AssetTypeCategory = "",
                    myObj.AssetTypeCategoryName = "",
                    myObj.AssetTypeCategoryCode = "",
                    // myObj.AttachmentName = "",
                    myObj.DisplayImage = "",
                    // myObj.IsTermsAndCondition = "",
                    myObj.TotalTermsConditionFilesCount = "",
                    myObj.RelatedInfoId = "",
                    myObj.UniqueName = "",
                    myObj.AssetCatalogueGroup = "",
                    myObj.ChildCreateUniqueNameListing = "",
                    myObj.IsTransitItems = "",
                    myObj.AttachmentPath = "",
                    myObj.LocationName = "",
                    myObj.SubLocationName = "",
                    myObj.AuditFrequency = vm.AssetCatalogData.AUDIT_FREQUENCY==""? "" : vm.AssetCatalogData.AUDIT_FREQUENCY,
                    myObj.ImageCaptureFrequency = vm.AssetCatalogData.IMAGE_CAPTURE_FREQUENCY==""? "": vm.AssetCatalogData.IMAGE_CAPTURE_FREQUENCY,
                    myObj.CalibrationFrequency = vm.AssetCatalogData.CALIBRATION_FREQUENCY == "" ? "" : vm.AssetCatalogData.CALIBRATION_FREQUENCY,
                    myObj.CR = "",
                    myObj.DR = "",
                    myObj.IsDisplayImage = "",
                    myObj.PurchaseValue = "",
                    myObj.CurrentBookValue = "",
                    myObj.SalvageValue = "",
                    myObj.Unit = "",
                    myObj.ReqFrom = "",
                    myObj.IsContainer = "",
                    myObj.IsSampleData = "",
                    myObj.Model = "",
                    myObj.Vendor = "",
                    myObj.ManufacturerId = "",
                    myObj.PurchaseOrder = "",
                    myObj.WarrantyDuration = "",
                    myObj.DepreciationRuleId = "",
                    myObj.OrderDate = "",
                    myObj.SalvageCost = "",
                    myObj.ItemLife = "",
                    myObj.InvoiceNumber = "",
                    myObj.InvoiceDate = "",
                    myObj.InsurerName = "",
                    myObj.InsuranceRefNumber = "",
                    myObj.InsuranceTo = "",
                    myObj.DeployedBy = "",
                    myObj.DeployedAt = "",
                    myObj.EnableWarranty = "",
                    myObj.EnableInsurance = "",
                    myObj.EnableItemCost = "",
                    myObj.EnableCalibration = "",
                    myObj.EnableDepreciation = "";
                
                var formData = new FormData();
                var postString = { "postString": myObj }
                debugger
                formData.append("postString", JSON.stringify(myObj));
                if(vm.AttachmentDivData.length >=0)
           {
            vm.fileUploaded.forEach((item,index) => {
                debugger
                var uploadFile=item;
               // formData.append("Files"+index, item[index]);
                formData.append('File'+index, uploadFile, item.name);
            });           }
            
               
                formData.append("AttachmentName", myObj.AttachmentName);
                formData.append("IsTermsAndCondition", myObj.IsTermsAndCondition);
                formData.append("Is_DisplayImage", 'No');
                DataService.UpdateCatalog(formData).then(function (response) {
                    if(response.data.msg =="size_exceed" )
                        {
                            vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "falure", true, vm.$t("Alert")))
                        }
                        else if(response.data.msg=='ValidFileUploadMsg')
                        {
                            vm.ShowAlert(vm.$t('InvalidFileUploadMsg'), "falure", true, vm.$t("Alert"));
                        }
                        else{  
                            vm.ShowAlert(vm.$t("Product has been updated Successfully"), "success",vm.$t('Alert'));
                            vm.$router.push({ path: '/AssetCatalog/Index'});
                        }
                    // vm.isLoading = false;
                })
            }
            else {
                // vm.isLoading = false;
                vm.ShowAlert((vm.$t('Unauthorized', [vm.$t('Asset')]), "faliure", true, vm.$t("Alert")))
            }
        },

        DownLoadFile(item){
            var vm=this;
            debugger; 
            var url = `fileId=${item.ASSET_ATTACHMENT_ID.toString()}&filePath=${item.ATTACHMENT_PATH}&fileName=${item.ATTACHMENT_NAME}&fileType=${item.ATTACHMENT_TYPE}`;            
            DataService.Downloadfile(url).then(response => {
                debugger;
                if (response != null) {
                vm.isLoading=false;
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', (item.ATTACHMENT_NAME));
                document.body.appendChild(fileLink);
                fileLink.click();
            }

                // if (response != null) {
                //     debugger;
                //     if (response.data!=null) {
                //         vm.FileData = response.data;
                //         var fileType = response.headers['content-type'];
                //         var fileName ="abc";// JSON.parse(response.headers.fileName);
                                          
                //      debugger;
                //     // Convert the Base64 string to a byte array
                //     const byteCharacters = atob(vm.FileData);
                //     const byteNumbers = new Array(byteCharacters.length);
                //     for (let i = 0; i < byteCharacters.length; i++) {
                //         byteNumbers[i] = byteCharacters.charCodeAt(i);
                //     }
                //     const byteArray = new Uint8Array(byteNumbers);

                //     // Create a Blob from the byte array
                //     const blob = new Blob([byteArray], { type: fileType });

                //     // Create a temporary URL and trigger the download
                //     const url = URL.createObjectURL(blob);
                //     const link = document.createElement('a');
                //     link.href = url;
                //     link.setAttribute('download', fileName + '.pdf');
                //     link.click();
                //     URL.revokeObjectURL(url);                                      
                //      vm.isLoading=false;
                //     }else {
                //         console.error('File not found');
                //     }
                // }
                // else {                       
                //      vm.InfoData = [];                        
                //      vm.isLoading=false;
                //  }                
            });            
                    

        },
        FetchData() {
            debugger
            var vm = this;
            var url = `catalogueId=${this.$route.params.id}&isDelete=1&flag=`;
            DataService.ViewFiles(url).then(response => {
                // if (JSON.parse(response.data ).data  != null) {
                    if (JSON.parse(response.data ).data.length > 0) {
                        vm.AttachmentList = JSON.parse(response.data ).data ;
                        vm.TotalRecords = vm.AttachmentList.length;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.AttachmentList = [];
                        vm.TotalRecords = 0
                    }
                // }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        clickEventOfColumn:function(row, column){
            
            if(column.COLUMN_NAME == 'LOCATION_NAME'){
                this.$router.push({name: 'ManageProducts', params:{id:row.ASSET_CATALOGUE_ID}
            });
            }
        },
        columnDataFormatter: function(row, column){
             return row[column.COLUMN_NAME];
           
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        }, 
    }
}
</script>