<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width:900px !important">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('DeployProductLocationWise') }}</h4>

                        <button type="button" class="close" v-on:click="ClosePopup">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="tagForm" autocomplete="off">
                            <div class="col-md-12">
                                <div class="form-group">
                                    <div class="listing OuterMainDiv">
                                        <div class="row" style="max-height:500px;overflow-y:auto;">
                                            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons"
                                                :schema="FormSchema" v-on:OnSubmit="onSubmitDeploy"
                                                ref="tagForm1"></dynamic-form>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </Form>
                    </div>

                    <div class="modal-header theme-primary partition-full" v-if="ClickType == 'Add'">
                        <h4 class="modal-title">{{ $t('Deploy List') }}</h4>
                    </div>
                    <div class="listing tblassestgroupinfodetails px-1 mt-3" v-if="ClickType == 'Add'">
                        <div class="table-responsive ">
                            <table id="tblassestgroupinfodetails" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">

                                    <tr>
                                        <th class="text-center"><span>{{ $t('LOCATION') }}</span></th>
                                        <th class="text-center"><span>{{ $t('Quantity') }}</span></th>
                                        <th class="text-center"><span>{{ $t('UnitPrice') }}</span></th>
                                        <th><span>{{ $t('Model') }}</span></th>
                                        <th><span>{{ $t('Manufacturer') }}</span></th>
                                        <th><span>{{ $t('Vendor') }}</span></th>
                                        <th class="text-center"><span>{{ $t('ProductCostQty') }}</span></th>
                                        <th class="text-center"><span>{{ $t('RefPurchaseOrder') }}</span></th>
                                        <th class="text-center"><span>{{ $t('OrderDate') }} </span></th>
                                        <th><span>{{ $t('InvoiceNumber') }}</span></th>
                                        <th><span>{{ $t('InvoiceDate') }}</span></th>
                                        <th><span>{{ $t('NameOfInsurer') }}</span></th>
                                        <th><span>{{ $t('InsuranceReferenceNumber') }}</span></th>
                                        <th><span>{{ $t('InsuranceTo') }}</span></th>
                                        <th><span>{{ $t('WarrantyDuration') }}</span></th>
                                        <th><span>{{ $t('DepreciationRule') }}</span></th>
                                        <th><span>{{ $t('SalvageCost') }}</span></th>
                                        <th><span>{{ $t('ItemLife') }}</span></th>
                                        <th><span>{{ $t('Action') }}</span></th>
                                        <th><span>{{ $t('CalibrationFrequency') }}</span></th>

                                    </tr>

                                </thead>





                                <tbody>
                                    <tr v-for="(item, index) in arryaofData" :key="index">
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.Location }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.Quantity }}</span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.UnitPrice }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.Model }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.Manufacture }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.Vendor }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.ProductCostQty }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.RefPurchaseOrder }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ $options.filters.formatDateTime(
                                                    item.OrderDate
                                                ) }}
                                            </span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.InvoiceNumber }}</span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ $options.filters.formatDateTime(
                                                    item.InvoiceDate
                                                ) }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.NameofInsurer }}</span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.InsuranceReferenceNumber
                                            }}</span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ $options.filters.formatDateTime(
                                                    item.InsuranceValidUpto
                                                ) }}
                                            </span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ $options.filters.formatDateTime(
                                                    item.WarrantyDuration
                                                ) }}
                                            </span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.DepreciationRule }}</span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.SalvageCost }}</span>
                                        </td>

                                        <td>
                                            <span class="text-overflow-dynamic-container">{{ item.ProductLifeInYears
                                            }}</span>
                                        </td>

                                        <td id="action_1" class="single-action text-center"><a href="javascript:;"
                                                class="deleteDataRow" title="Delete">
                                                <i class="fa fa-trash action-icon text-danger"></i>
                                            </a>
                                        </td>
                                    </tr>

                                    <tr v-if="noRecord">
                                        <td colspan="8" class="text-center text-danger no-record">{{ $t('NoRecordfound') }}
                                        </td>
                                    </tr>
                                </tbody>
                            </table>

                        </div>
                    </div>

                    <div v-if="arryaofData.length > 0" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0 m-2">
                        <div class="row flex-row-reverse m-0">
                            <div class="col-lg-4 text-right">

                                <a href="javascript:;" v-on:click="SubmitData()"
                                    class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i
                                        class="fa fa-save pr-2"></i>
                                    <span>{{ $t('Save') }}</span></a>
                                <a href="javascript:;" @click="ClosePopup()" :title="$t('Cancel')"
                                    class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{
                                        $t('Cancel') }}</span></a>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common'
export default {
    components: {
        'tree-view':TreeSelectField
    },
    props: {
        ClickType:
        {
            type: String
        },
        AsseCatalogtId: {
            type: String
        },
        DeployItemId: {
            type: String
        }
    },

    data() {
        return {

            arryaofData: [],
            LocationData: [],



            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select Location",
                            config: {
                                options: [],
                            }

                        },
                        {
                            astype: "RadioField",
                            label: this.$t('DeploymentType'),
                            name: "DeployMentType",
                            value: "1",
                            placeholder: "",
                            config: {
                                options: [{ value: "1", name: "Product" }, { value: "2", name: "Inventory" }],
                                onChange: this.OnDeplymentType,
                                //CallBackFunction: null
                            },

                            validationRules: { "required": false },

                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Quantity'),
                            name: "Quantity",
                            value: "",
                            placeholder: "",
                            validationRules: "required",


                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Unit Price'),
                            name: "UnitPrice",
                            value: '',
                            placeholder: "",
                            validationRules: "required",
                            // onchange: this.calcSum
                            config: {
                                onChange: this.calcSum
                            }
                        },

                        {
                            astype: "TextField",
                            label: this.$t('Model'),
                            name: "Model",
                            value: '',
                            placeholder: "",

                            // config: { format: "MM/DD/YYYY" }
                        },

                        {
                            astype: "TextField",
                            label: this.$t('Manufacture'),
                            name: "Manufacture",
                            value: '',
                            placeholder: "",

                        },

                        {
                            astype: "TextField",
                            label: this.$t('Vendor'),
                            name: "Vendor",
                            value: "",
                            placeholder: "",

                        },

                        {
                            astype: "NumericField",
                            label: this.$t('ProductCostQty'),
                            name: "ProductCostQty",
                            value: "",
                            placeholder: "",
                            disabled: true,

                            validationRules: "required",

                        },

                        {
                            astype: "TextField",
                            label: this.$t('RefPurchaseOrder'),
                            name: "RefPurchaseOrder",
                            value: "",
                            placeholder: "",


                        },

                        {
                            astype: "DateField",
                            label: this.$t('OrderDate'),
                            name: "OrderDaTe",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "M/D/YYYY" }
                        },

                        {
                            astype: "TextField",
                            label: this.$t('InvoiceNumber'),
                            name: "InvoiceNumber",
                            value: "",
                            placeholder: "",


                        },

                        {
                            astype: "DateField",
                            label: this.$t('InvoiceDate'),
                            name: "InvoiceDate",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "M/D/YYYY" }
                        },


                        {
                            astype: "TextField",
                            label: this.$t('InsuranceReferenceNumber'),
                            name: "InsuranceReferenceNumber",
                            value: "",
                            placeholder: "",
                            disabled: true,


                        },

                        {
                            astype: "TextField",
                            label: this.$t('Name of Insurer'),
                            name: "NameofInsurer",
                            value: "",
                            placeholder: "",
                            disabled: true,


                        },

                        {
                            astype: "DateField",
                            label: this.$t('InsuranceValidUpto'),
                            name: "InsuranceValidUpto",
                            value: '',
                            placeholder: "10/12/2022 12:AM",
                            disabled: true,

                            config: { format: "M/D/YYYY" }
                        },


                        {
                            astype: "DateField",
                            label: this.$t('WarrantyDuration'),
                            name: "WarrantyDuration",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "M/D/YYYY" }
                        },

                        {
                            astype: "SelectField",
                            label: this.$t('DepreciationRule'),
                            name: "DepreciationRule",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            config: {
                                options: [],

                            },

                        },

                        {
                            astype: "TextField",
                            label: this.$t('ProductLifeInYears'),
                            name: "ProductLifeInYears",
                            value: "",
                            placeholder: "",
                            disabled: true,


                        },

                        {
                            astype: "TextField",
                            label: this.$t('Salvage Cost'),
                            name: "SalvageCost",
                            value: "",
                            placeholder: "",


                        },

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-Primary",
                    text: "<i class='fa fa-plus pr-2'></i>Add To List",
                    isVisible: true
                },
                {
                    type: "button",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save ",
                    isVisible: false,
                    onClick: this.SubmitData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    isVisible: false,
                    onClick: this.ClosePopup
                }



            ],
            DataLoaded: 0,
            LocationName:''

        };
    },
    created: async function () {
        debugger;
        var vm = this;
        if (vm.ClickType == 'Update') {
            vm.BindDataFileds();
            vm.buttons[0].isVisible = false;
            vm.buttons[1].isVisible = true;
            vm.buttons[2].isVisible = true;
        }
        else if (vm.ClickType == 'Add') {
            vm.buttons[0].isVisible = true;
        }
        await vm.GetLocation();
        vm.DataLoaded = 1;
    },
    methods: {
        OnDeplymentType(event, field) {
            field.value = event.target.getAttribute('data-value');
        },
        ClosePopup() {
            debugger
            var vm = this;
            //   vm.$parent.;
            vm.$parent.DeployClosePOp();

        },
       
       async  GetLocation() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `reqForm=`
            await DataService.GetLocationByuserId(params).then(response => {
                vm.LocationData = response.data.data;             
               
                vm.FindalLocationArray = [];
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
            })
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        calcSum(val) {
            var vm = this
            let ProductCost = 0;
            ProductCost = (vm.FormSchema)[0].Data[2].value * (vm.FormSchema)[0].Data[3].value;
            (vm.FormSchema)[0].Data[7].value = ProductCost;

        },

        onSubmitDeploy: function (formfields, val) {
            var vm = this;          
            vm.arryaofData.push(formfields);
            let LocationObj = vm.LocationData.filter((item) => {
                return item.COMPANY_LOCATION_ID == formfields.Location;
            }); 
            vm.LocationName=LocationObj[0].LOCATION_NAME;
        },

        // resetinput() {
        //     this.Quantity = "";
        // },

        async SubmitData() {
            debugger;
            var vm = this;
            var ArrayData = [];
            // this.$parent.test();
            let myObj = {
                DeployId: 0,
                AssetCatalogueId: 102250,
                AssetTypeId: 40587,
                LocationId: 150529,
                LocationName: (vm.FormSchema)[0].Data[0].value,
                Quantity: (vm.FormSchema)[0].Data[2].value,
                UnitPrice: (vm.FormSchema)[0].Data[3].value,
                Model: (vm.FormSchema)[0].Data[4].value,
                Manufacturer: (vm.FormSchema)[0].Data[5].value,
                Vendor: (vm.FormSchema)[0].Data[6].value,
                PurchaseOrder: (vm.FormSchema)[0].Data[8].value,
                WarrantyDuration: (vm.FormSchema)[0].Data[15].value,
                DepreciationRuleId: 0,
                OrderDate: (vm.FormSchema)[0].Data[9].value,
                CalibrationFrequency: null,
                AssetCost: (vm.FormSchema)[0].Data[7].value,
                SalvageCost: (vm.FormSchema)[0].Data[18].value,
                ItemLife: null,
                InvoiceNumber: (vm.FormSchema)[0].Data[10].value,
                InvoiceDate: (vm.FormSchema)[0].Data[11].value,
                InsurerName: null,
                InsuranceRefNumber: null,
                InsuranceTo: null,
                DeployedBy: null,
                DeployedAt: "0001-01-01T00:00:00",
                EnableWarranty: false,
                EnableInsurance: false,
                EnableItemCost: false,
                EnableCalibration: false,
                EnableDepreciation: false,
                DeploymentType: (vm.FormSchema)[0].Data[1].value,
            }
            ArrayData.push(myObj);
            var JsonObj = JSON.stringify(ArrayData);
            await DataService.SaveDeploy(ArrayData).then(function (response) {
                debugger
                console.log(response);
                vm.ClosePopup();
                //this.$parent.IscheckAddDeployProduct=false;
                vm.$parent.FetchData();
                vm.ShowAlert(vm.$t('Product has been successfully deployed successfully added.',), "success", true, vm.$t("Alert"))
                //this.$router.push('AssetCatalog/ManageProducts/102250')

            })

        },
        async BindDataFileds() {

            var vm = this;
            var url = `DeployItemId=${vm.DeployItemId}&AssetCatalogId=${this.$route.params.id}`;
            await DataService.GetDeployItems(url).then(response => {

                if (JSON.parse(response.data.result) != null) {
                    var fieldDataValues = JSON.parse(response.data.result);
                    if (JSON.parse(response.data.result).length > 0) {
                        vm.FormSchema[0].Data[0].value = fieldDataValues[0].LOCATION_NAME;
                        vm.FormSchema[0].Data[1].value = fieldDataValues[0].deployement_type == "1" ? '1' : '2';;
                        vm.FormSchema[0].Data[2].value = fieldDataValues[0].quantity;
                        vm.FormSchema[0].Data[3].value = fieldDataValues[0].unit_price;
                        vm.FormSchema[0].Data[4].value = fieldDataValues[0].model;
                        vm.FormSchema[0].Data[5].value = fieldDataValues[0].manufacturer;
                        vm.FormSchema[0].Data[6].value = fieldDataValues[0].vendor;
                        vm.FormSchema[0].Data[7].value = fieldDataValues[0].asset_cost;
                        vm.FormSchema[0].Data[8].value = fieldDataValues[0].purchase_order;
                        vm.FormSchema[0].Data[9].value = fieldDataValues[0].order_date;
                        vm.FormSchema[0].Data[10].value = fieldDataValues[0].invoice_number;
                        vm.FormSchema[0].Data[11].value = fieldDataValues[0].invoice_date;
                        vm.FormSchema[0].Data[12].value = fieldDataValues[0].insurance_ref_number;
                        vm.FormSchema[0].Data[13].value = fieldDataValues[0].insurer_name;
                        vm.FormSchema[0].Data[14].value = fieldDataValues[0].insurance_valid_upto;
                        vm.FormSchema[0].Data[15].value = fieldDataValues[0].warranty_duration;
                        vm.FormSchema[0].Data[16].value = fieldDataValues[0].depreciation_rule;
                        vm.FormSchema[0].Data[17].value = fieldDataValues[0].item_life;
                        vm.FormSchema[0].Data[18].value = fieldDataValues[0].salvage_cost;
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });

        }
    },

};


</script>