<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-lg-12 mt-2">
                <div class="heading-border mb-2">
                    <h5 class="h5">
                        <span>{{ $t('DeployItem') }}</span>
                        <small class="float-right mt-2">
                            <a id="ancDeployItems" class="AddDeployItem" title="Add" @click="addDeployProduct">
                                <i class="fa fa-plus pr-2"></i>Add
                            </a>
                        </small>
                    </h5>
                </div>
            </div>
        </div>
        <div class="container-fluid d-flex flex-wrap py-2">
            <div class="right-content">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left pb-2 p-0">
                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                        <div class="row mt-0">
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                <div class="form-group mb-0">
                                    <input id="assetSearch" v-model="txtSearchByLocation"  class="form-control" type="text"
                                        placeholder="Search by Location" />
                                </div>
                            </div>
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                <div class="search-btm-btn background-0 text-left form-check-inline">
                                    <a id="aSearchAsset" v-on:click="SearchByLocation()"  title="Search"
                                        class="btn btn-success float-left"><i class="fa fa-search"></i></a>
                                    <a id="aUN_ClearSearch" href="javascript:;" v-on:click="ClearSearch" class="btn btn-danger ml-1 float-left"
                                        title="Clear"><i class="fa fa-refresh"></i></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
        <tg-list :IsShowAction="false" SearchPosition='none' :showCheckBox="false"
        :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="Asset Management" :ListData="DeployItemData"
            :HeaderData="Headers" :callbackfunction="FetchData" 
            :TotalRecords="TotalRecords" 
            :NorecordfoundText="$t('NoRecordfound')"
            :ListDataCallBackFunction="FetchData"
             v-on:PagerButtonClick="pagerMethod"
            >
            <template v-slot:slotdata="{ data }">
        <template v-if="data.column.COLUMN_NAME == 'DEPLOYEMENT_TYPE'">
            <span class="text-dark" :title="data.row.NUMBER_OF_ITEM">
                        
                        <span class="">{{ data.row.DEPLOYEMENT_TYPE == 1 ? 'Product' : 'Inventory' }}</span>
                    </span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'QUANTITY'">
                    <span class="text-dark" :title="data.row.QUANTITY">
                        <i class="fa fa-list" aria-hidden="true"></i>
                        <span class="noti-circle noti-blue">{{ data.row.QUANTITY > 0 ? data.row.QUANTITY : 0 }}</span>
                    </span>
            </template>
        </template>
        </tg-list>
        &nbsp;
        <div class="p-3">
        <dynamic-form lang="en" :buttons="buttons" ></dynamic-form>
    </div> 
        <AddDeployProductpop v-if="IscheckAddDeployProduct" :DeployItemId="DeployItemId" :ClickType="ClickType" @DeployClosePOp="DeployClosePOp"> </AddDeployProductpop>
        
    </div>
   
</template>


<script>
import AddDeployProductpop from '../../components/Modal/AddDeployProductpop';
import DataService from '../../service/DataService'
export default {
    components:{
        AddDeployProductpop,
    },
    data() {
        return {
            Headers: [{
                "COLUMN_NAME": "LOCATION_NAME",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Location'),
                "DISPLAY_ORDER": 1,
                settings : {
                            clickEvent: this.clickEventOfColumn,
                            formatter: this.columnDataFormatter,
                        }

            },
            {
                "COLUMN_NAME": "MODEL",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('model'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "PURCHASE_ORDER",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('PurchaseOrder'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "MANUFACTURER",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Manufacturer'),
                "DISPLAY_ORDER": 4,


            },
            
            {
                "COLUMN_NAME": "INVOICE_NUMBER",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('BillNumber'),
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "ORDER_DATE",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('order_date'),
                "DISPLAY_ORDER": 4,


            },
            {
                "COLUMN_NAME": "DEPLOYEMENT_TYPE",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('DeploymentType'),
                "DISPLAY_ORDER": 6,
                settings : {
                            isInSlot : true
                        }
            },

            {
                "COLUMN_NAME": "DEPLOYED_BY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('DeployedBy'),
                "DISPLAY_ORDER": 7,
            },
            {
                "COLUMN_NAME": "MODIFIED_AT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('DeployedAt'),
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "QUANTITY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Quantity'),
                "DISPLAY_ORDER": 9,
                settings : {
                            isInSlot : true
                        }
            },
            {
                "COLUMN_NAME": "ASSET_COST",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AssetCost'),
                "DISPLAY_ORDER": 10,
            }

            ],
            DeployItemData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            IscheckAddDeployProduct: false,
            
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning HideTab",
                    text: "Previous <i class='fa fa-angle-double-left pr-2'></i>",
                    isVisible: true,  
                    onClick: this.onPreviousClick
                },
                {
                    type: "submit",
                    class: "btn btn-primary HideTab",
                    text: "Next<i class='fa fa-angle-double-right pl-2'></i>",
                    isVisible: true,
                    onClick: this.onNextClick
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save ",
                    isVisible: false,
                    onClick:this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    isVisible: true,
                    onClick: this.onCancel
                }
            ],
            ClickType:'',
            AsseCatalogtId:'',
            DeployItemId:'',
            txtSearchByLocation:''
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
    },
    methods:
    {
        onCancel()
        {
            var vm=this;
            vm.$parent.btnCancel();
        },
        onNextClick()
        {
            var vm=this;
            vm.$parent.btnNextTab();
        },
        onPreviousClick()
        {
            debugger
            var vm=this;
            vm.$parent.btnPreviousTab();
        },
        DeployClosePOp: function () {
            var vm = this;
            vm.IscheckAddDeployProduct = false;
        },
        addDeployProduct: function(){
           
            var vm = this;
            vm.ClickType='Add'
            vm.IscheckAddDeployProduct = true;
        },
        SearchByLocation()
        {
            var vm=this;
            vm.searchBy=vm.txtSearchByLocation == '' ? '' : vm.txtSearchByLocation;
            vm.FetchData();
        },
        ClearSearch()
        {
            var vm=this;
            vm.searchBy='';
            vm.txtSearchByLocation='';
            vm.FetchData();
        },
        FetchData() {
            var vm = this;
            var url = `search=${vm.searchBy == '' ? '' : vm.txtSearchByLocation}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&reqstatus=&LocationId=&catalogId=${this.$route.params.id}`;
            DataService.ShowDeployItems(url).then(response => {
                debugger
                if (JSON.parse(response.data ).data  != null) {
                    if (JSON.parse(response.data ).data .length > 0) {


                        vm.DeployItemData = JSON.parse(response.data ).data ;
                        vm.TotalRecords = vm.DeployItemData.length;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.DeployItemData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        clickEventOfColumn:function(row, column){
            var vm=this;
            if(column.COLUMN_NAME == 'LOCATION_NAME'){
                vm.ClickType='Update';
                vm.AsseCatalogtId=row.ASSET_CATALOGUE_ID;
                vm.DeployItemId=row.DEPLOY_ITEM_ID;
                vm.IscheckAddDeployProduct=true;
            }
        },
        columnDataFormatter: function(row, column){
             return row[column.COLUMN_NAME];
           
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        }, 
    }
}
</script>